ourcesContent":["@import url(\"https://cdn-uicons.flaticon.com/2.2.0/uicons-bold-rounded/css/uicons-bold-rounded.css\");\n\n@mixin exo2($weight: 400) {\n    font-family: \"Exo 2\", sans-serif;\n    font-weight: $weight;\n    font-optical-sizing: auto;\n    font-style: normal;\n}\n\n$main-theme-color: #3c486b;\n$main-theme-color-bright: #fff3cf;\n\n$side-bar-color: #f8f8f88e;\n\n* {\n    margin: 0;\n    padding: 0;\n}\n\nbody {\n    font-size: 12px;\n    display: flex;\n    flex-direction: column;\n    width: 100vw;\n    height: 100vh;\n}\n\nheader {\n    display: flex;\n    justify-content: center;\n    width: 100%;\n    height: 15vh;\n    background-color: #1e1e1e;\n    box-shadow: rgba(0, 0, 0, 0.25) 1px 5px 5px;\n    top: 0;\n\n    #div_logo,\n    #div_theme {\n        display: flex;\n        align-self: center;\n        justify-content: center;\n    }\n\n    #div_logo {\n        position: static;\n        top: 1rem;\n    }\n\n    #div_theme {\n        position: relative;\n        left: 15rem;\n        .switch {\n            --button-width: 4em;\n            --button-height: 2em;\n            --toggle-diameter: 1.5em;\n            --button-toggle-offset: calc((var(--button-height) - var(--toggle-diameter)) / 2);\n            --toggle-shadow-offset: 10px;\n            --toggle-wider: 3em;\n            --color-grey: #3c486b;\n            --color-green: #cfa833;\n        }\n\n        #slider {\n            display: inline-block;\n            width: var(--button-width);\n            height: var(--button-height);\n            background-color: var(--color-grey);\n            border-radius: calc(var(--button-height) / 2);\n            position: relative;\n            transition: 0.3s all ease-in-out;\n        }\n\n        #slider::after {\n            content: \"\";\n            display: inline-block;\n            width: var(--toggle-diameter);\n            height: var(--toggle-diameter);\n            background-color: #ffffff;\n            border-radius: calc(var(--toggle-diameter) / 2);\n            position: absolute;\n            top: var(--button-toggle-offset);\n            transform: translateX(var(--button-toggle-offset));\n            box-shadow: var(--toggle-shadow-offset) 0 calc(var(--toggle-shadow-offset) * 4) rgba(0, 0, 0, 0.1);\n            transition: 0.3s all ease-in-out;\n        }\n\n        .switch input[type=\"checkbox\"]:checked + #slider {\n            background-color: var(--color-green);\n        }\n\n        .switch input[type=\"checkbox\"]:checked + #slider::after {\n            transform: translateX(calc(var(--button-width) - var(--toggle-diameter) - var(--button-toggle-offset)));\n            box-shadow: calc(var(--toggle-shadow-offset) * -1) 0 calc(var(--toggle-shadow-offset) * 4)\n                rgba(0, 0, 0, 0.1);\n        }\n\n        .switch input[type=\"checkbox\"] {\n            display: none;\n        }\n\n        .switch input[type=\"checkbox\"]:active + #slider::after {\n            width: var(--toggle-wider);\n        }\n\n        .switch input[type=\"checkbox\"]:checked:active + #slider::after {\n            transform: translateX(calc(var(--button-width) - var(--toggle-wider) - var(--button-toggle-offset)));\n        }\n    }\n\n    #title {\n        @include exo2($weight: 700);\n        font-size: 3rem;\n        text-align: center;\n        padding: 16px;\n        color: #fff;\n    }\n\n    .fi {\n        font-size: 2rem;\n    }\n\n    img {\n        width: 10%;\n        align-self: center;\n        padding-right: 1rem;\n    }\n}\n\n#content,\n.content-bright {\n    display: flex;\n    justify-content: center;\n    background-color: $main-theme-color;\n    height: 85vh;\n    #side-bar {\n        display: flex;\n        flex-direction: column;\n        width: 30%;\n        height: 85vh;\n        background-color: $side-bar-color;\n        border-top-right-radius: 40px;\n        border-bottom-right-radius: 40px;\n        justify-content: flex-start;\n        align-items: center;\n        gap: 1rem;\n        overflow: scroll;\n        @include exo2();\n\n        #your-todo-list {\n            display: flex;\n            flex-direction: column;\n            width: 100%;\n            justify-content: center;\n            align-items: center;\n            gap: 1rem;\n            #h1AndButton {\n                display: flex;\n                justify-content: space-between;\n                width: 100%;\n                h1 {\n                    @include exo2($weight: 700);\n                    font-size: 2rem;\n                    text-align: center;\n                    padding: 16px;\n                    color: #1e1e1e;\n                }\n                #tab-controller {\n                    margin-right: 2rem;\n                    background: none;\n                    border: none;\n                    img {\n                        width: 70%;\n                        &:hover {\n                            cursor: pointer;\n                            width: 90%;\n                        }\n                    }\n                }\n            }\n            .addingSthButton,\n            .addingSthButton-bright {\n                img {\n                    width: 20%;\n                }\n                background: #fff;\n                width: 15rem;\n                border-width: 0;\n                border-radius: 50px;\n                font-size: 1rem;\n                text-align: center;\n                vertical-align: middle;\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                &:hover {\n                    color: #fff;\n                    background-color: #1e1e1e;\n                    border-radius: 40px;\n                    cursor: pointer;\n                }\n            }\n            .addingSthButton-bright {\n                background: $main-theme-color;\n                color: #fff;\n                &:hover {\n                    color: $main-theme-color;\n                    background-color: #fff;\n                    border: #3c486b solid 1px;\n                    border-radius: 40px;\n                    cursor: pointer;\n                }\n            }\n            .tabButton {\n                display: flex;\n                align-items: center;\n                justify-content: left;\n                padding-left: 6rem;\n                gap: 2rem;\n                &:hover {\n                    background-color: #fff;\n                    border-radius: 40px;\n                    border: #3c486b solid 1px;\n                    cursor: pointer;\n                }\n                background: none;\n                width: 100%;\n                border: none;\n                font-size: 1.5rem;\n                text-align: center;\n            }\n        }\n        #projects {\n            display: flex;\n            flex-direction: column;\n            width: 100%;\n            justify-content: center;\n            align-content: flex-start;\n            gap: 1rem;\n            h1 {\n                @include exo2($weight: 700);\n                font-size: 2rem;\n                text-align: left;\n                padding: 16px;\n                color: #1e1e1e;\n            }\n            .addingSthButton,\n            .addingSthButton-bright {\n                img {\n                    width: 20%;\n                }\n                background: #fff;\n                width: 15rem;\n                border-width: 0;\n                border-radius: 50px;\n                font-size: 1rem;\n                text-align: center;\n                vertical-align: middle;\n                display: flex;\n                align-self: center;\n                align-items: center;\n                justify-content: center;\n                &:hover {\n                    color: #fff;\n                    background-color: #1e1e1e;\n                    border-radius: 40px;\n                    cursor: pointer;\n                }\n            }\n            .addingSthButton-bright {\n                background: $main-theme-color;\n                color: #fff;\n                &:hover {\n                    color: $main-theme-color;\n                    background-color: #fff;\n                    border: #3c486b solid 1px;\n                    border-radius: 40px;\n                    cursor: pointer;\n                }\n            }\n            .tabProject {\n                display: flex;\n                align-items: center;\n                justify-content: left;\n                padding-left: 6rem;\n                gap: 2rem;\n                &:hover {\n                    background-color: #fff;\n                    border-radius: 40px;\n                    border: #3c486b solid 1px;\n                }\n                button:first-child {\n                    background: none;\n                    width: fit-content;\n                    border: none;\n                    font-size: 1.5rem;\n                    text-align: center;\n                    display: flex;\n                    align-items: center;\n                    &:hover {\n                        cursor: pointer;\n                    }\n                }\n                button:last-child {\n                    background: none;\n                    border: none;\n                    img {\n                        width: 70%;\n                        &:hover {\n                            cursor: pointer;\n                            width: 90%;\n                        }\n                    }\n                }\n            }\n        }\n    }\n    #rightPart {\n        display: flex;\n        flex-direction: column;\n        width: 70%;\n        height: 85vh;\n        align-items: center;\n        overflow: scroll;\n        .aTodo,\n        .aTodo-bright {\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n            gap: 2rem;\n            background-color: #d9d9d9;\n            width: 60%;\n            height: 8rem;\n            border-top-left-radius: 40px;\n            border-bottom-left-radius: 40px;\n            margin-top: 2rem;\n            margin-bottom: 2rem;\n            .colorSign {\n                // background-color: #6ccf33;\n                width: 4rem;\n                height: 8rem;\n                // border-color: #cfa833;\n                border-radius: 50px;\n            }\n            .shortInfo {\n                @include exo2(700);\n                font-size: 1.2rem;\n            }\n            .todoSetting {\n                display: flex;\n                flex-direction: column;\n                height: 8rem;\n                width: 8rem;\n                justify-content: center;\n                gap: 0.5rem;\n                font-size: 1rem;\n                @include exo2();\n                .setting {\n                    display: flex;\n                    align-items: center;\n\n                    img {\n                        height: 2rem;\n                    }\n                    &:hover {\n                        font-size: 1.5rem;\n                        cursor: pointer;\n                        img {\n                            height: 2.5rem;\n                        }\n                    }\n                }\n            }\n            .checkbox {\n                height: 8rem;\n                width: 8rem;\n                background-color: #fff;\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                img {\n                    display: none;\n                    height: fit-content;\n                    width: 5rem;\n                }\n                &.checked {\n                    img {\n                      display: flex;\n                    }\n                  }\n                &:hover {\n                    cursor: pointer;\n                    img {\n                        display: flex;\n                    }\n                }\n            }\n        }\n        .aTodo-bright {\n            background-color: $main-theme-color;\n            color: #d9d9d9;\n        }\n    }\n    button#tab-controller {\n        position: absolute;\n        top: 9vh;\n        left: 0px;\n        background: none;\n        border: none;\n        img {\n            width: 70%;\n            &:hover {\n                cursor: pointer;\n                width: 90%;\n            }\n        }\n    }\n}\n"],"sourceRoot":""}]);
